import { Keyboard } from '@ionic-native/keyboard';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the KeyboardPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-keyboard',
  templateUrl: 'keyboard.html',
})
export class KeyboardPage {

  resizeModes: any = ['body','native','ionic']
  resizeMode: string = 'ionic';
  isHideAccessarybar: boolean = true;
  result : any;
  constructor(public navCtrl: NavController,
     public navParams: NavParams,
     private keyboard: Keyboard) {

      this.keyboard.onKeyboardHide().subscribe(res=>{
        this.result += "onKeyboardHide:" + JSON.stringify(res, null ,2)
      },err=>{
        this.result += "onKeyboardHide:" + JSON.stringify(err, null ,2)
      })

      this.keyboard.onKeyboardShow().subscribe(res=>{
        this.result += "onKeyboardShow:" + JSON.stringify(res, null ,2)
      },err=>{
        this.result += "onKeyboardShow:" + JSON.stringify(err, null ,2)
      })

      this.keyboard.onKeyboardWillHide().subscribe(res=>{
        this.result += "onKeyboardWillHide:" + JSON.stringify(res, null ,2)
      },err=>{
        this.result += "onKeyboardWillHide:" + JSON.stringify(err, null ,2)
      })

      this.keyboard.onKeyboardWillShow().subscribe(res=>{
        this.result += "onKeyboardWillShow:" + JSON.stringify(res, null ,2)
      },err=>{
        this.result += "onKeyboardWillShow:" + JSON.stringify(err, null ,2)
      })
  }

  setResizeMode(){
    this.keyboard.setResizeMode(this.resizeMode)
  }

  sethideAccessaryBar(){
    this.keyboard.hideFormAccessoryBar(this.isHideAccessarybar)
  }

 
  acceptModifed(){ 
    console.log(this.resizeMode,this.isHideAccessarybar)
    this.setResizeMode()
    this.keyboard.hideFormAccessoryBar(this.isHideAccessarybar)
    
  }

  clearContent(){
    this.result = ''
  }
  

}



